import common from '../../common/common.js'
export default {
	data() {
		return {
			rowsList: [],
			myKeywords: '', // 搜索的关键字
			page: 1, // 当前第几页
			totalPages: 0 // 总页数
		}
	},
	onLoad() {
		this._getSearchPageData()
	},
	onReachBottom() { // 上拉加载触底的函数
		let page = this.page + 1
		let myKeywords = this.myKeywords
		let totalPages = this.totalPages
		if (page > totalPages) {
			return false
		}
		this.pagedTrailerList(myKeywords, page, 15)
	},
	methods: {
		_getSearchPageData() {
			uni.showLoading({
				title: '加载中',
				mask: true
			});
			uni.request({
				url: common.api_base_url + '/search/list?keywords=&page=&pageSize=',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: common.qq
				},
				method: 'POST',
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						this.rowsList = res.data.data.rows
					} else {
						this.rowsList = []
					}
				}),
				fail: ((err) => {
					console.log(err)
					this.rowsList = []
				}),
				complete: (() => {
					uni.hideLoading()
				})
			})
		},
		searchMe(e) {
			this.myKeywords = e.detail.value
			this.rowsList = []
			this.pagedTrailerList(this.myKeywords, 1, 15)
		},
		pagedTrailerList(keywords, page, pageSize) {
			uni.showLoading({
				title: '加载中',
				mask: true
			});
			uni.request({
				url: common.api_base_url + '/search/list?keywords=' + keywords + '&page=' + page + '&pageSize=' + pageSize,
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data: {
					qq: common.qq
				},
				method: 'POST',
				success: ((res) => {
					if (res.data.status === 200 && res.data.msg === 'OK') {
						const templateData = res.data.data.rows
						this.rowsList = this.rowsList.concat(templateData)
						this.totalPages = res.data.data.total
						this.page = page
					}
				}),
				fail: ((err) => {
					console.log(err)
				}),
				complete: (() => {
					uni.hideLoading()
				})
			})
		},
		gotoMovie(e) {
			let id = e.currentTarget.dataset.id
			uni.navigateTo({
				url: `../movie/movie?id=${id}`
			})
		}
	}
}
